






































import { Component, Vue, Prop, Watch, Emit } from 'vue-property-decorator';
import { currencyFormatter } from '@utils/util';
@Component
export default class MonitorChart extends Vue {
  // @Prop() monitorName: String;
  @Prop() block: any;
  @Prop({ default: [] }) dataSource: any;
  @Emit('getDataByCard') getDataByCard(info) {}
  private customColor: any = '#409eff';
  private all: any = 0;
  private completed: any = 0;
  // private percentage: number = 0;

  get percentage() {
    return this.completed && this.all
      ? currencyFormatter((this.completed / this.all) * 100)
      : 0;
  }

  @Watch('dataSource')
  onDataSourceChange() {
    const goods = this.block.goods;
    this.all =
      this.dataSource.length > 0 ? this.dataSource[0][goods.allCount] : 0;
    this.completed =
      this.dataSource.length > 0 ? this.dataSource[0][goods.completeCount] : 0;
  }

  handleQueryData() {
    if (this.block && this.block.goods.monitorName) {
      this.getDataByCard({
        type: 'monitorSteps',
        monitorName: this.block.goods.monitorName
      });
    }
  }
}
