












import { cloneDeep } from 'lodash';
import { Component, Vue, Prop, Emit } from 'vue-property-decorator';
import { updateTableData } from '@api/pageTemplate';
import { namespace } from 'vuex-class';
const pageTemplate = namespace('pageTemplate');

@Component
export default class TableChart extends Vue {
  @Prop({ default: [] }) dataSource: Array<any>;
  @Prop() block: any;
  @pageTemplate.Mutation('UPDATE_ACTIVE_BLOCK') updateActiveBlock;

  @Emit('changeGrid') changeGrid() {}
  @Emit('displayTemplateDetail') displayTemplateDetail(id) {}

  get columns() {
    const columns = (this.block.goods && this.block.goods.columns) || [];
    columns.forEach((item: any) => {
      if (item.canSet) {
        item.render = (h: any, { record }) => {
          if (record.isEdit === true) {
            return h('el-input', {
              props: {
                value: record[item.prop],
                clearable: true,
                placeholder: '请输入参数code'
              },
              on: {
                input: (value) => {
                  record[item.prop] = value;
                  this.handleUpdateData(record);
                }
              }
            });
          } else {
            return h('span', record[item.prop]);
          }
        };
      } else {
        item.render = null;
      }
    });
    const currentBlock = cloneDeep(this.block);
    currentBlock.goods.columns = columns;
    // this.updateActiveBlock(currentBlock);
    this.changeGrid();
    return columns;
  }
  get height() {
    return this.dataSource.length ? this.dataSource.length * 32 + 50 : '100%';
  }

  @Emit('queryRelationDataset') queryRelationDataset(info) {}

  cellClick(row, column, cell, event) {
    this.queryRelationDataset({
      type: 'chart',
      cell: row[column.property],
      relationBlock: this.block.id
    });
  }

  // 表格可编辑列内容修改
  handleUpdateData(row: any) {
    updateTableData(row).then(({ error }: { error: any }) => {
      if (error) {
        this.$message.error(error.message);
      } else {
        this.displayTemplateDetail(this.$route.query.id);
      }
    });
  }
}
