



import { Component, Vue, Prop, Watch, Emit } from 'vue-property-decorator';
import echarts from 'echarts';

@Component
export default class PieChart extends Vue {
  @Prop() block: any;
  @Prop({ default: [] }) dataSource: any;

  get label() {
    return (this.block.goods && this.block.goods.label) || '';
  }

  get value() {
    return (this.block.goods && this.block.goods.value) || '';
  }

  get pieType() {
    return (this.block.goods && this.block.goods.pieType) || '';
  }

  @Emit('queryRelationDataset') queryRelationDataset(info) {}

  @Watch('dataSource')
  onDataSourceChange() {
    this.initChart();
  }
  @Watch('label')
  onLabelChange() {
    this.initChart();
  }
  @Watch('value')
  onValueChange() {
    this.initChart();
  }
  @Watch('pieType')
  onPieTypeChange() {
    this.initChart();
  }

  private chart: any = null;
  private options: any = {
    tooltip: {
      trigger: 'item'
    },
    color: [
      'rgba(42, 118, 205, 0.85)',
      'rgba(43, 188, 184, 0.85)',
      'rgba(68, 179, 101, 0.85)',
      'rgba(234, 169, 38, 0.85)',
      'rgba(204, 61, 61, 0.85)',
      'rgba(133, 84, 201, 0.85)',
      'rgba(60, 58, 154, 0.85)',
      'rgba(58, 71, 119, 0.85)'
    ],
    legend: {
      orient: 'vertical',
      left: 'left',
      data: []
    },
    series: []
  };

  resizeChart() {
    setTimeout(() => {
      this.chart && this.chart.resize();
    }, 100);
  }

  initChart() {
    this.chart = echarts.init(this.$el as any);
    this.chart.setOption(this.options);

    this.refreshChart();
    this.clickChart();
  }

  refreshChart() {
    if (this.dataSource) {
      if (this.label) {
        this.chart.setOption({
          legend: {
            data: this.dataSource.map((item: any) => item[this.label])
          }
        });
      }
      let radius: any = '55%';
      if (this.pieType === '1') {
        radius = ['50%', '70%'];
      } else if (this.pieType === '2') {
        radius = ['20%', '70%'];
      }
      if (this.value) {
        const name = this.label || this.value;
        this.chart.setOption({
          series: [
            {
              name: '',
              type: 'pie',
              radius: radius,
              center: ['50%', '60%'],
              roseType: this.pieType === '2' ? 'radius' : false,
              data: this.dataSource.map((item: any) => {
                return {
                  value: item[this.value],
                  name: item[name]
                };
              }),
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: 'rgba(0, 0, 0, 0.5)'
                },
                normal: {
                  borderColor: '#ffffff',
                  borderWidth: 1
                }
              }
            }
          ]
        });
      }
    }
  }

  clickChart() {
    this.chart.off('click');
    this.chart.on('click', (param) => {
      this.queryRelationDataset({
        type: 'chart',
        label: param.name,
        value: param.value,
        relationBlock: this.block.id
      });
    });
  }
}
