
















import { Component, Vue, Prop, Emit } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import { cloneDeep } from 'lodash';
import { formDetail, saveFormData } from '@api/customForm';
import perviewForm from '@views/processConfig/customForm/compoents/previewForm.vue';

const pageTemplate = namespace('pageTemplate');

@Component({
  components: { perviewForm }
})
export default class CustomForm extends Vue {
  @pageTemplate.Mutation('UPDATE_ACTIVE_BLOCK') updateActiveBlock;
  @pageTemplate.State((state) => state.activeBlock) activeBlock: any;

  @Prop({ default: {} }) block: any;
  @Prop({ default: {} }) activeFormItem: any;
  @Prop() preview: boolean;
  @Prop({ default: [] }) beRelatedFromItemList: any;

  @Emit('activeFormItemChange') updateActiveFormItem(item: any) {}
  @Emit('queryRelationDataset') queryRelationDataset(info) {}

  private query: any = '';

  get currentBlock() {
    return cloneDeep(this.activeBlock);
  }

  get formAttribute() {
    return (this.block.goods && this.block.goods.formAttribute) || {};
  }

  // 根据详情地址 获取返显数据
  created(): void {
    if (this.$route.query && JSON.stringify(this.$route.query) !== '{}') {
      this.query = this.$route.query;
      this.getTemplateDetail();
    }
  }

  // 当前块中被其他图表作为视图参数的表单项
  get formItemQueryParams() {
    const formItemQueryParams: any = [];
    this.formAttribute.formList &&
      this.formAttribute.formList.map((item) => {
        if (
          this.beRelatedFromItemList.some((innerItem) => item.key === innerItem)
        ) {
          formItemQueryParams.push({
            formItemKey: item.key,
            formItemValue: item.value
          });
        }
      });
    return formItemQueryParams;
  }

  stopBubble(e?) {
    // 如果提供了事件对象，则这是一个非IE浏览器
    if (e && e.stopPropagation)
      // 因此它支持W3C的stopPropagation()方法
      e.stopPropagation();
    // 否则，我们需要使用IE的方式来取消事件冒泡
    else (window.event as any).cancelBubble = true;
  }

  formItemClick(formItem) {
    this.stopBubble();
    if (this.currentBlock.id !== this.block.id) {
      this.updateActiveBlock(cloneDeep(this.block));
    }
    this.updateActiveFormItem(formItem);
  }

  itemDeleteClick(formItem, index) {
    if (this.currentBlock.id !== this.block.id) {
      this.updateActiveBlock(cloneDeep(this.block));
    }

    this.formAttribute.formList.splice(index, 1);
    this.updateActiveFormItem(
      this.formAttribute.formList.length ? this.formAttribute.formList[0] : {}
    );

    if (
      this.currentBlock &&
      this.currentBlock.goods &&
      this.currentBlock.goods.formAttribute
    ) {
      const formList = this.currentBlock.goods.formAttribute.formList;
      const i = formList.findIndex((item: any) => item.key === formItem.key);
      formList.splice(i, 1);
      this.updateActiveBlock(cloneDeep(this.currentBlock));
    }
  }

  handleFormItemChange(val, formItem) {
    const activeFormItem =
      formItem.key !== this.activeFormItem.key
        ? cloneDeep(formItem)
        : cloneDeep(this.activeFormItem);
    activeFormItem.value = val;
    this.updateActiveFormItem(activeFormItem);
  }

  loop = (formAttribute, data) => {
    for (let i = 0; i < formAttribute.length; i++) {
      if (formAttribute[i].type !== 'childFrom') {
        formAttribute[i].editable = 1;
        formAttribute[i].readable = 0;
        formAttribute[i].value = data[formAttribute[i].key];
      } else {
        Object.keys(data).forEach((res) => {
          if (
            formAttribute[i].value.formList !== undefined &&
            formAttribute[i].key === res
          ) {
            this.loop(formAttribute[i].value.formList, data[res]);
          }
        });
      }
    }
  };

  washFormEditable(formList) {
    formList &&
      formList.forEach((formItem) => {
        if (formItem.type !== 'childFrom') {
          formItem.editable = 1;
          formItem.readable = 0;
        } else {
          this.washFormEditable(formItem.value.formList);
        }
      });
  }

  // 获取页面模板详情
  getTemplateDetail() {
    const paramId: any = this.query;
    const url: any = this.formAttribute.detailAddress;
    const params = {
      url,
      paramId
    };
    if (this.formAttribute.detailAddress) {
      formDetail(params).then(({ data, error }: any) => {
        if (!error) {
          this.loop(this.formAttribute.formList, data);
        } else {
          this.$message.error(error.message || '获取数据失败');
        }
      });
    } else {
      this.washFormEditable(this.formAttribute.formList);
    }
  }

  // 表单参数
  paramsObtain() {
    const param = {};
    // 不校验当前页面隐藏（editable为1），但标记为必输的表单项字段
    if (Object.prototype.hasOwnProperty.call(this.formAttribute, 'formList')) {
      this.formAttribute.formList.forEach((item: any) => {
        // 只保存显示的表单项值
        if (
          (!item.showForm || item.showForm === 'show') &&
          (item.editable === 1 || item.readable === 1)
        ) {
          if (item.type === 'table') {
            // 删除编辑状态
            if (item.datas) {
              item.datas.forEach((item) => {
                delete item['edits'];
                delete item['beModified'];
              });
              this.$set(param, item.key, item.datas);
            }
          } else if (item.type === 'childFrom') {
            const childParam = {};
            item.value.formList.forEach((itemChild) => {
              if (itemChild.type === 'table') {
                if (itemChild.datas) {
                  itemChild.datas.forEach((item) => delete item['edits']);
                  this.$set(childParam, itemChild.key, itemChild.datas);
                }
              } else {
                this.$set(childParam, itemChild.key, itemChild.value);
              }
            });
            this.$set(param, item.key, childParam);
          } else if (Object.prototype.hasOwnProperty.call(item, 'realValue')) {
            this.$set(param, item.key, item.realValue);
          } else {
            this.$set(param, item.key, item.value);
          }
        }
      });
    }
    Object.keys(this.query).forEach((res) => {
      this.$set(param, res, this.query[res]);
    });
    return {
      param
    };
  }

  vaildForm() {
    let vaild = true;
    if (this.formAttribute.formList) {
      const tempArr = this.formAttribute.formList.filter((item) => {
        return (!item.showForm || item.showForm === 'show') && item.editable;
      });
      if (
        tempArr.some(
          (item) => item.type !== 'table' && item.required && !item.value
        )
      ) {
        this.$message.warning('请先完成必填表单项输入');
        vaild = false;
      }
    }
    return vaild;
  }

  // 收集表单信息
  saveFormData = () => {
    if (this.vaildForm() && this.formAttribute.saveAddress !== undefined) {
      const paramsObtain = this.paramsObtain().param;
      const saveAddress = this.formAttribute.saveAddress;
      const params = {
        paramsObtain,
        saveAddress
      };
      saveFormData(params).then(({ data, error }) => {
        if (error) {
          this.$message.error(error.message);
        } else {
          this.$message.success('保存成功');
        }
      });
    }
  };
}
