





















































































































import { Component, Vue, Prop, Emit, Watch } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import { cloneDeep } from 'lodash';
import TableBlock from './tableChart.vue';
import PieBlock from './pieChart.vue';
import LineBlock from './lineChart.vue';
import BarBlock from './barChart.vue';
import CustomForm from './CustomForm.vue';
import MonitorChart from './monitorChart.vue';
import MonitorSteps from './monitorSteps.vue';
const pageTemplate = namespace('pageTemplate');

@Component({
  components: {
    TableBlock,
    PieBlock,
    LineBlock,
    BarBlock,
    CustomForm,
    MonitorChart,
    MonitorSteps
  }
})
export default class TemplateBlock extends Vue {
  @Prop({ default: false }) preview: boolean;
  @Prop() block: any;
  @Emit('getRealContentById') getRealContentById(block) {}

  get blockQueryParams() {
    return (
      (this.block.goods &&
        this.block.goods.queryParams &&
        this.block.goods.queryParams.filter((item) => item.valueBy === '1')) ||
      []
    );
  }
  get componentkey() {
    let result = '';
    if (this.block.goods) {
      const type = this.block.goods.type;
      if (type === 'table') {
        result = 'TableBlock';
      } else if (type === 'pie') {
        result = 'PieBlock';
      } else if (type === 'line') {
        result = 'LineBlock';
      } else if (type === 'bar') {
        result = 'BarBlock';
      } else if (type === 'singleForm' || type === 'doubleForm') {
        result = 'CustomForm';
      } else if (type === 'monitorChart') {
        result = 'MonitorChart';
      } else if (type === 'monitorSteps') {
        result = 'MonitorSteps';
      }
    }
    return result;
  }

  resizeChart() {
    const refs: any = this.$refs;
    refs.componentBlock.resizeChart && refs.componentBlock.resizeChart();
  }
}
