
































import { Component, Vue, Prop, Watch, Emit } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
const pageTemplate = namespace('pageTemplate');
import { cloneDeep } from 'lodash';

@Component
export default class MonitorSteps extends Vue {
  @Prop({ default: {} }) block: any;
  @Prop() dataSource: any;
  @Emit('getDataByCard') getDataByCard(info) {}

  get stepsList() {
    const list =
      this.block && this.block.goods.stepsList
        ? cloneDeep(this.block.goods.stepsList)
        : [];
    if (list.length && this.dataSource && this.dataSource.length) {
      list.forEach((item: any, index: any) => {
        item.all =
          item.allCount && this.dataSource[index] && this.dataSource[index][0]
            ? this.dataSource[index][0][item.allCount]
            : 0;
        item.completed =
          item.completeCount &&
          this.dataSource[index] &&
          this.dataSource[index][0]
            ? this.dataSource[index][0][item.completeCount]
            : 0;
        item.uncomplete = item.all - item.completed;
        item.percentage = (item.completed / item.all) * 100;
      });
    }
    return list;
  }

  // 根据卡片名称查询表格数据
  handleQueryData() {
    const monitorName = this.stepsList.map((item: any) => {
      return item.monitorName;
    });
    if (monitorName.length) {
      this.getDataByCard({
        type: 'monitorSteps',
        monitorName: monitorName
      });
    }
  }
}
