



























import { Component, Vue, Prop } from 'vue-property-decorator';
import Base64 from '@utils/Base64';
import { pageTemplateDetail } from '@api/pageTemplate';
import { dataViewQuery } from '@api/viewData';
import { arrayFind } from '@utils/util';
import TemplateBlock from './templateBlock.vue';
import { cloneDeep } from 'lodash';
import { getFormatter, getGridCOLUMNS } from '../utils/util';

@Component({
  components: { TemplateBlock }
})
export default class PreviewTemplate extends Vue {
  @Prop() templateId?: any; // 传过来了模板id，此时需要根据id查询模板详情
  @Prop() currentGrid?: Array<any>; // 直接将模板所需gird传过来了，不需要查询详情，可以直接显示

  private previewGrid: any = null; // 弹窗的布局及对应数据源
  private multipleData: any = {}; // 数据源详细数据
  private loading = false;

  get COLUMNS() {
    return getGridCOLUMNS(this.previewGrid);
  }

  // 当前页面中所有被设置为视图参数的表单项
  get beRelatedFromItemList() {
    const beRelatedFromItemList: Array<any> = [];
    if (this.previewGrid) {
      this.previewGrid.map((item) => {
        if (item && item.goods && item.goods.queryParams) {
          const formList = item.goods.queryParams.filter(
            (innerItem) => innerItem.valueBy === '2'
          );
          formList.map((i) => {
            if (beRelatedFromItemList.indexOf(i.relationFormItem) === -1) {
              beRelatedFromItemList.push(i.relationFormItem);
            }
          });
        }
      });
    }
    return beRelatedFromItemList;
  }

  // 初始化每个块的获取数据源参数，根据参数值获取方式区分出来; 还在这里获取了真实数据
  initBlockParamsAndContent(initGoodsBlocks?: any) {
    this.previewGrid.forEach((item: any) => {
      if (initGoodsBlocks) {
        const block = arrayFind(initGoodsBlocks, 'blockId', item.goods);
        if (block.type === 'table') {
          block.columns.forEach((item) => {
            if (item.format) {
              item.formatter = getFormatter(item.format);
            }
          });
        }
        item.goods = block;
      }

      this.getRealContentById(item);
    });
  }

  // 打开预览弹窗
  initPreviewTemplate() {
    if (this.templateId) {
      this.getTemplateDetail();
    } else {
      this.previewGrid = cloneDeep(this.currentGrid);
      this.initBlockParamsAndContent();
    }
  }

  // 获取页面模板详情
  getTemplateDetail() {
    this.loading = true;
    pageTemplateDetail({ id: this.templateId }).then(({ data, error }: any) => {
      this.loading = false;
      if (!error) {
        const grid = JSON.parse(Base64.decode(data.style));
        const blocks = data.blocks;
        this.previewGrid = grid;
        // blocks: 如果是在列表页预览，则需要根据id获取到模板详情，获取到的详情数据中的good是一个对应于 blocks 中blockId的字符串
        this.initBlockParamsAndContent(blocks);
      } else {
        this.$message.error(error.message || '获取数据失败');
      }
    });
  }

  // 获取每个块的真实数据
  getRealContentById(block: any) {
    if (block.goods && block.goods.type !== 'monitorSteps') {
      if (!block.goods || !block.goods.dataSetId) {
        return;
      }
      const param: any = { viewId: block.goods.dataSetId };
      const queryParams: any = [];
      for (let i = 0, len = block.goods.queryParams.length; i < len; i++) {
        const queryParam = block.goods.queryParams[i];
        if (queryParam.required === 0 && !queryParam.value) {
          // 必选参数为空时不做查询
          // this.$message.warning(`${block.goods.blockName}中数据源必选参数为空！`);
          // this.$set(this.multipleData, block.id, []);
          return;
        }
        queryParams.push({
          code: queryParam.code,
          type: queryParam.type,
          value: queryParam.value
        });
      }
      param.queryParams = queryParams;

      dataViewQuery(param).then(({ data, error }: any) => {
        if (!error) {
          if (block.goods.type === 'table') {
            data.rows.forEach((item: any) => {
              item.isEdit = true;
            });
          }
          this.$set(this.multipleData, block.id, data.rows);
        } else {
          this.$message.error(error.message || '获取数据失败');
        }
      });
    } else {
      const list = block.goods ? block.goods.stepsList : [];
      list.forEach((item: any, index: any) => {
        if (item.dataSetId) {
          const queryParam: any = { viewId: item.dataSetId };
          const preview: any = [];
          if (block.goods.queryParams) {
            block.goods.queryParams.forEach((item) => {
              if (item.code === 'monitorName') {
                item.value = block.goods.queryName;
              }
              preview.push({
                code: item.code,
                type: item.type,
                value: item.value
              });
            });
          }
          queryParam.previewParams = preview;
          dataViewQuery(queryParam).then(
            ({ data, error }: { data: any; error: any }) => {
              if (!error) {
                const dataList = cloneDeep(this.multipleData);
                if (this.multipleData[block.id]) {
                  dataList[block.id][index] = data.rows;
                  this.multipleData = dataList;
                } else {
                  dataList[block.id] = [];
                  dataList[block.id][index] = data.rows;
                  this.multipleData = dataList;
                }
              } else {
                this.$message.error(error.message || '获取数据失败');
              }
            }
          );
        }
      });
    }
  }

  // 根据关联图表或表单项查询数据
  queryRelationDataset(info: any) {
    if (!this.previewGrid) {
      return;
    }
    this.previewGrid.forEach((item) => {
      if (item.goods && item.goods.queryParams) {
        let query = false;
        item.goods.queryParams.forEach((innerItem) => {
          // 点击图表某一区域触发与之相关联图表的查询
          if (info.type === 'chart') {
            if (
              innerItem.valueBy === '3' &&
              innerItem.relationChart === info.relationBlock
            ) {
              query = true;
              this.$set(innerItem, 'value', info[innerItem.relationChartField]);
            }
          }

          // 点击表单块中的查询按钮触发与表单项关联的图表数据查询
          if (info.type === 'formItem') {
            if (innerItem.valueBy === '2') {
              for (let i = 0, len = info.formItems.length; i < len; i++) {
                const formItem = info.formItems[i];
                if (innerItem.relationFormItem === formItem.formItemKey) {
                  query = true;
                  this.$set(innerItem, 'value', formItem.formItemValue);
                  break;
                }
              }
            }
          }
        });
        if (query) {
          this.getRealContentById(item);
        }
      }
    });
  }

  // 监控卡片关联表格数据
  getDataByCard(info) {
    if (!this.previewGrid) {
      return;
    }
    this.previewGrid.forEach((block) => {
      if (
        block.goods &&
        block.goods.type === 'table' &&
        block.goods.dataSetId
      ) {
        const params = {
          viewId: block.goods.dataSetId,
          monitorName: info.monitorName
        };
        dataViewQuery(params).then(
          ({ data, error }: { data: any; error: any }) => {
            if (!error) {
              data.rows.forEach((item: any) => {
                item.isEdit = true;
              });
              this.$set(this.multipleData, block.id, data.rows);
            } else {
              this.$message.error(error.message || '获取数据失败');
            }
          }
        );
      }
    });
  }
}
