



import { Component, Vue, Prop, Watch, Emit } from 'vue-property-decorator';
import echarts from 'echarts';

@Component
export default class BarChart extends Vue {
  @Prop({ default: [] }) dataSource: any;
  @Prop() block: any;

  get xaxis() {
    return (this.block.goods && this.block.goods.xaxis) || '';
  }
  get yaxis() {
    return (this.block.goods && this.block.goods.yaxis) || [];
  }

  @Emit('queryRelationDataset') queryRelationDataset(info) {}

  @Watch('dataSource')
  onDataSourceChange() {
    this.initChart();
  }
  @Watch('xaxis')
  onXAxisChange() {
    this.initChart();
  }
  @Watch('yaxis')
  onYAxisChange(newVal: any, oldVal) {
    if (JSON.stringify(newVal) !== JSON.stringify(oldVal)) {
      this.initChart();
    }
  }

  private chart: any = null;

  resizeChart() {
    setTimeout(() => {
      this.chart && this.chart.resize();
    }, 100);
  }

  initChart() {
    this.chart = echarts.init(this.$el as any);
    this.refreshData();
    this.clickChart();
  }

  refreshData() {
    if (this.dataSource) {
      let xData: Array<any> = [];
      const seriesData: Array<any> = [];
      this.yaxis.forEach((items) => {
        xData = [];
        let obj: any = {};
        const seriesList: Array<any> = [];
        this.dataSource.forEach((item: any) => {
          xData.push(item[this.xaxis]);
          seriesList.push(item[items.code]);
        });
        obj = {
          name: items.label,
          data: seriesList,
          type: 'bar'
        };
        seriesData.push(obj);
      });
      this.chart.setOption(
        {
          color: [
            'rgba(42, 118, 205, 0.85)',
            'rgba(43, 188, 184, 0.85)',
            'rgba(68, 179, 101, 0.85)',
            'rgba(234, 169, 38, 0.85)',
            'rgba(204, 61, 61, 0.85)',
            'rgba(133, 84, 201, 0.85)',
            'rgba(60, 58, 154, 0.85)',
            'rgba(58, 71, 119, 0.85)'
          ],
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              // 坐标轴指示器，坐标轴触发有效
              type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
            }
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          yAxis: {
            type: 'value'
          },
          xAxis: {
            name: '',
            type: 'category',
            axisTick: {
              alignWithLabel: true
            },
            axisLabel: {
              rotate: -20
            },
            data: xData
          },
          series: seriesData
        },
        true
      );
    }
  }

  clickChart() {
    this.chart.off('click');
    this.chart.on('click', (param) => {
      this.queryRelationDataset({
        type: 'chart',
        x: param.name,
        y: param.value,
        relationBlock: this.block.id
      });
    });
  }
}
