import { arrayFind, currencyFormatter, thousandthFormatter } from '@utils/util';
import { cloneDeep } from 'lodash';

export function changeBlockFields(dataNode, block) {
  let fun = null;
  switch (block.goods.type) {
    case 'table':
      fun = changeTableConfig;
      break;
    case 'pie':
      fun = changePieConfig;
      break;
    case 'line':
      fun = changLineConfig;
      break;
    case 'bar':
      fun = changBarConfig;
      break;
    case 'monitorChart':
      fun = changeMonitorChartConfig;
      break;
    default:
  }
  if (dataNode.data) {
    return fun(dataNode.data, block.goods);
  } else {
    return fun(dataNode, block.goods);
  }
}

function changeTableConfig(node, goods) {
  // 调整table的columns配置，如果还有其他配置，也可以在这儿完成
  if (!goods.columns) {
    goods.columns = [];
  }
  if (node.fields) {
    node.fields.forEach((item) => {
      if (!arrayFind(goods.columns, 'fieldCode', item.fieldCode)) {
        const newData = {};
        newData.label = item.fieldName;
        newData.prop = item.fieldCode;
        newData.fieldCode = item.fieldCode;
        newData.align = item.align || 'center';
        newData.headerAlign = 'center';
        newData.fieldType = item.fieldType;
        newData.showOverflowTooltip = true;
        newData.canSet = item.canSet || 0;

        if (item.fieldType === 1) {
          newData.format = item.format || '';
        } else if (item.fieldType === 2) {
          newData.format = item.format || 'yyyy-MM-dd';
        } else {
          newData.format = '';
        }
        newData.formatter = getFormatter(item.format);
        goods.columns.push(newData);
      }
    });
  } else {
    if (!arrayFind(goods.columns, 'fieldCode', node.fieldCode)) {
      const newData = {};
      newData.label = node.fieldName;
      newData.prop = node.fieldCode;
      newData.fieldCode = node.fieldCode;
      newData.align = node.align || 'center';
      newData.headerAlign = 'center';
      newData.fieldType = node.fieldType;
      newData.showOverflowTooltip = true;
      newData.canSet = node.canSet || 0;

      if (node.fieldType === 1) {
        newData.format = node.format || '';
      } else if (node.fieldType === 2) {
        newData.format = node.format || 'yyyy-MM-dd';
      } else {
        newData.format = '';
      }
      newData.formatter = getFormatter(node.format);
      goods.columns.push(newData);
    }
  }
}

function changePieConfig(node, goods) {
  if (node.fields) {
    return '只能拖入节点，不能拖入整个数据集';
  } else {
    if (node.attrFlag === 'pieLabel') {
      if (!goods.label) {
        goods.label = node.fieldCode;
        goods.pieType = node.pieType;
      } else {
        return 'repeat';
      }
    }
    if (node.attrFlag === 'pieValue') {
      if (!goods.value) {
        goods.value = node.fieldCode;
        goods.pieType = node.pieType;
      } else {
        return 'repeat';
      }
    }
  }
}

function changLineConfig(node, goods) {
  if (node.fields) {
    return '只能拖入节点，不能拖入整个数据集';
  } else {
    if (node.attrFlag === 'xaxis') {
      if (!goods.label) {
        goods.xaxis = node.fieldCode;
      } else {
        return 'repeat';
      }
    }
    if (node.attrFlag === 'yaxis') {
      if (!goods.yaxis) {
        goods.yaxis = [];
      }
      const lineChartArr = goods.yaxis;
      if (!arrayFind(lineChartArr, 'fieldCode', node.fieldCode)) {
        lineChartArr.push({
          code: node.fieldCode,
          label: node.name
        });
      } else {
        return 'repeat';
      }
    }
  }
}

function changBarConfig(node, goods) {
  if (node.fields) {
    return '只能拖入节点，不能拖入整个数据集';
  } else {
    if (node.attrFlag === 'xaxis') {
      if (!goods.label) {
        goods.xaxis = node.fieldCode;
      } else {
        return 'repeat';
      }
    }
    if (node.attrFlag === 'yaxis') {
      if (!goods.yaxis) {
        goods.yaxis = [];
      }
      const barArr = goods.yaxis;
      if (!arrayFind(barArr, 'fieldCode', node.fieldCode)) {
        barArr.push({
          code: node.fieldCode,
          label: node.name
        });
      } else {
        return 'repeat';
      }
    }
  }
}

function changeMonitorChartConfig(node, goods) {
  if (node.fields) {
    return '只能拖入节点，不能拖入整个数据集';
  } else {
    if (node.attrFlag === 'completeCount') {
      goods.completeCount = node.fieldCode;
    } else if (node.attrFlag === 'allCount') {
      goods.allCount = node.fieldCode;
    }
  }
}

// 获取布局的行数
export function getGridROWS(grid) {
  return (
    grid.reduce((item1, item2) =>
      item1.gridRowEnd > item2.gridRowEnd ? item1 : item2
    ).gridRowEnd - 1
  );
}

// 获取布局的列数
export function getGridCOLUMNS(grid) {
  return (
    grid.reduce((item1, item2) =>
      item1.gridColumnEnd > item2.gridColumnEnd ? item1 : item2
    ).gridColumnEnd - 1
  );
}

// 格式化方法
export function getFormatter(type) {
  let fun = null;
  switch (type) {
    case 'thousand':
      fun = function(row, column) {
        return thousandthFormatter(row[column.property]);
      };
      break;
    case 'amount':
      fun = function(row, column) {
        return currencyFormatter(row[column.property]);
      };
      break;
    case 'yyyy-MM-dd':
      fun = function(row, column) {
        return dateFormat(row[column.property], '-');
      };
      break;
    case 'yyyy/MM/dd':
      fun = function(row, column) {
        return dateFormat(row[column.property], '/');
      };
      break;
    default:
      fun = function(row, column) {
        return row[column.property];
      };
  }
  return fun;
}

function dateFormat(date, type) {
  const d = new Date(date);
  const year = d.getFullYear();
  let month = d.getMonth() + 1;
  let day = d.getDate();
  if (month < 10) month = `0${month}`;
  if (day < 10) day = `0${day}`;
  return `${year}${type}${month}${type}${day}`;
}

export function randomString(length, chars) {
  let result = '';
  for (let i = length; i > 0; --i) {
    result += chars[Math.floor(Math.random() * chars.length)];
  }
  return result;
}
