









import { Component, Vue } from 'vue-property-decorator';
import PreviewTemplate from './components/previewTemplate.vue';
@Component({
  components: { PreviewTemplate }
})
export default class DynamicPage extends Vue {
  private pageTitle = '自定义页面';
  private templateId = '';

  mounted() {
    (this.$refs.previewTemplate as any).initPreviewTemplate();
  }

  created() {
    this.templateId = this.$route.params.id;
    this.pageTitle = this.$route.params.dynamicPageTitle;
  }
}
